
import saleRepotService from '../service/saleRepotService'
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import querystring from 'vux/src/tools/querystring/index.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'


export default {
components:{
Alert,
Cell,
Group,
Divider,
XButton,
Confirm,
LoadMore,
FormPreview,
CellFormPreview,
},
data(){
return{
list1:[],
list2:[],
list3:[],
list4:[],
total1:'',
total2:'',
total3:'',
total4:'',
childInfo:''
}
},
filters:{
numberComma
},
methods:{
/*
* 
*/
sendOrder(){
let that=this;
this.$vux.confirm.show({
title: '',
content: '',
confirmText:"",
cancelText:"",
onConfirm () {
saleRepotService.subAmount(querystring.stringify(that.childInfo)).then(data=>{
if(data.success){
that.$vux.alert.show({
title: '',
content: data.message,
onHide () {
that.$router.replace('/Home');
localStorage.removeItem('saleReport');
localStorage.removeItem('saleReportInfo');
}
})

}
}).catch(data=>{
if(data.success == false){
that.$vux.alert.show({
title: '',
content: data.message,
})
}
})
}
})

},

info(){
let total1=0;
let total2=0;
let total3=0;
let jsonData=JSON.parse(this.childInfo.jsonData).transDetailUncalc;
for(let i = 0 ;i<jsonData.length; i++){
if(jsonData[i].containerCode==''){
this.list1.push({
label:jsonData[i].transObjCode,
value:jsonData[i].qty+'/',
});
this.list4.push({
label:'',
value:jsonData[i].qty,
})
total1 =jsonData[i].amount;

}else if(jsonData[i].containerCode=='A'){
this.list2.push({
label:jsonData[i].transObjCode,
value:jsonData[i].qty,
});
total2 =jsonData[i].amount;
}else if(jsonData[i].containerCode=='B'){
this.list3.push({
label:jsonData[i].transObjCode,
value:jsonData[i].qty,
});
total3 = jsonData[i].amount;
}
}
this.total1 = ''+total1;
this.total2 = ''+total2;
this.total3 = ''+total3;
let num = Number(total1)+Number(total2)+Number(total3);
this.total4 = ''+num;
}
},
mounted(){
this.childInfo = JSON.parse(localStorage.getItem('saleReportInfo')).saleReportRemark
this.info()
}
}



